.room {
  position: relative;
  width: 500px;
  height: calc(100vh - 80px);
  box-shadow: 1px 0 11px 1px rgba(0,0,0,0.88);

  & > .typing {
    position: absolute;
    bottom: 110px;
    right: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    pointer-events: none;
    transition: ease 5s;
    transform: translateY(100px) ;
    & > img {
      border-radius: 50%;
      width: 25px;
      margin-right: 5px;
    }
  }

  & > .typing.active {
    opacity: 1;
    pointer-events: all;
    transition: ease 0s;
    transform: translateY(0) ;
  }

  & > .messages {
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    height: calc(100vh - 185px);

    &::-webkit-scrollbar {
      width: 0;
    }

    & > .pagination {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      background: rgba(0, 0, 0, 0.3);
      padding: 3px 0;
    }

    & > .message-component.sender {
      display: flex;
      justify-content: end;
      align-items: center;


      & > div {
        display: flex;
        justify-content: center;

        div {
          & > img {
            width: 30px;
            border-radius: 50%;
            display: none;
          }
        }


        & > div {
          & > h1 {
            display: none;
          }
          & > p {
            font-size: 16px;
            background: #0095F6;
            padding: 5px 10px;
            margin: 5px;
            border-radius: 10px;
            color: white;
            word-wrap: break-word;
            max-width: 240px;

          }
        }


      }
    }

    & > .message-component.host {
      display: flex;
      justify-content: start;
      align-items: center;

      & > div {
        display: flex;
        justify-content: center;

        & > div {
          & > img {
            width: 30px;
            border-radius: 50%;
          }
        }


        & > div {

          & > h1 {
            font-weight: bold;
            padding: 0 5px;
          }
          & > p {
            font-size: 16px;
            background: rgba(0, 0, 0, 0.3);
            padding: 5px 10px;
            margin: 5px;
            border-radius: 10px;
            word-wrap: break-word;
            max-width: 240px;
          }
        }


      }
    }

  }

  & > .sendMessageForm {
    position: relative;
    width: 100%;
    & > textarea {
      padding: 10px;
      margin: 0;
      resize: none;
      width: 100%;
      height: 100px;
      border: 2px solid rgb(216 89 140);
    }

    & > button {
      border: none;
      background: transparent;
      cursor: pointer;
      position: absolute;
      top: 10px;
      right: 10px;
      & > i {
        font-size: 20px;
      }
    }
  }
}